.ScrollToTopButton {
  background-color: #1F7B98;
  border-radius: 25px;
  position: fixed; 
  width: 24px;
  bottom: 7px;
  right: 20px;
  align-items: center;
  height: 24px;
  justify-content: center;
  cursor: pointer;
  animation: fadeIn 0.3s;
  z-index: 100;

  .ScrollToTopButtonIcon {
    fill: white;
  }

  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
}

